// Componenta principal a aplicaiei - orchestrator pentru rutare ntre pagini
import React, { useEffect } from 'react';
import { Routes, Route, Navigate } from 'react-router-dom'; // Importuri react-router-dom

import TransactionsPage from './pages/TransactionsPage';
import LunarGridPage from './pages/LunarGridPage';
import OptionsPage from './pages/OptionsPage';
import LoginForm from './components/features/Auth/LoginForm';
import RegisterForm from './components/features/Auth/RegisterForm';
import { Toaster } from 'react-hot-toast';
import Spinner from './components/primitives/Spinner';
import NavLink from './components/primitives/NavLink';
import { TITLES } from '@shared-constants';
import { getEnhancedComponentClasses } from './styles/themeUtils';

// Import store Zustand pentru autentificare
import { useAuthStore } from './stores/authStore';
// Import pentru iniializarea categoriilor
import { useCategoryStore } from './stores/categoryStore';
import { CATEGORIES } from '@shared-constants/categories';

/**
 * Componenta principal a aplicaiei, refactorizat pentru a utiliza custom hooks i servicii
 * 
 * Structura:
 * 
 * Aceast structur separ clar logica de business de UI, crescnd testabilitatea, 
 * mentenabilitatea i facilitnd extinderea ulterioar.
 */
export const App: React.FC = () => {
  console.log(' App render using react-router-dom');
  
  const { user, loading, checkUser } = useAuthStore();
  
  // Iniializarea categoriilor la nivel global
  const loadCategories = useCategoryStore(state => state.loadUserCategories);
  const mergeWithDefaults = useCategoryStore(state => state.mergeWithDefaults);
  
  // Verificm dac utilizatorul este autentificat
  useEffect(() => {
    checkUser();
  }, [checkUser]);
  
  // Iniializm categoriile dup autentificare
  useEffect(() => {
    // Nu ncrcm categoriile dac utilizatorul nu este autentificat
    if (!user) return;
    
    console.log('[App] Iniializare categorii global');
    
    const initializeCategories = async () => {
      try {
        // 1. Mai nti ncrcm categoriile personalizate din DB
        await loadCategories(user.id);
        
        // 2. Apoi fuzionm cu cele predefinite din CATEGORIES (shared-constants)
        // Conversia e necesar pentru c CATEGORIES are un format uor diferit de CustomCategory[]
        const defaultCategories = Object.entries(CATEGORIES).map(([name, subcats]) => ({
          name,
          subcategories: Object.values(subcats).flat().map(subcatName => ({
            name: subcatName,
            isCustom: false
          })),
          isCustom: false
        }));
        
        // 3. Fuziune - prioritate pentru cele personalizate
        mergeWithDefaults(defaultCategories);
        
        console.log('[App] Categorii iniializate cu succes');
      } catch (error) {
        console.error('[App] Eroare la iniializarea categoriilor:', error);
      }
    };
    
    initializeCategories();
  }, [user, loadCategories, mergeWithDefaults]);
  
  // Afieaz spinner n timpul ncrcrii strii de autentificare
  if (loading) {
    return (
      <div className={getEnhancedComponentClasses('container', 'primary', undefined, 'loading', ['centered', 'fixed'])}>
        <Spinner sizeVariant="xl" />
      </div>
    );
  }

  // Rute protejate i publice
  // Dac utilizatorul nu este autentificat, i ncearc s acceseze o rut protejat, va fi redirecionat
  // la /login. Pagina de login va fi ruta implicit dac nu e logat.

  return (
    <>
      <Toaster position="top-right" toastOptions={{ duration: 3500 }} />
      <div className={getEnhancedComponentClasses('container', 'primary', 'lg')}>
        {user && ( /* Afieaz navigarea doar dac utilizatorul este logat */ 
          <div className={getEnhancedComponentClasses('navbar-container', 'primary') + ' mb-6'}>
            <NavLink to="/transactions" testId="transactions-tab">
              {TITLES.TRANZACTII}
            </NavLink>
            <NavLink to="/lunar-grid" testId="lunar-grid-tab">
              {TITLES.GRID_LUNAR}
            </NavLink>
            <NavLink to="/options" testId="options-tab">
              {TITLES.OPTIUNI || 'Opiuni'}
            </NavLink>
          </div>
        )}
        
        <Routes>
          {user ? (
            <>
              <Route path="/" element={<Navigate to="/transactions" replace />} />
              <Route path="/transactions" element={<TransactionsPage />} />
              <Route path="/lunar-grid" element={<LunarGridPage />} />
              <Route path="/options" element={<OptionsPage />} />
              {/* Orice alt rut pentru utilizator logat, redirecioneaz la tranzacii */}
              <Route path="*" element={<Navigate to="/transactions" replace />} />
            </>
          ) : (
            <>
              {/* Rute publice pentru login i register */}
              <Route path="/login" element={<LoginForm />} /> {/* Simplificat: LoginForm va avea link ctre /register */}
              <Route path="/register" element={<RegisterForm />} /> {/* Simplificat: RegisterForm va avea link ctre /login */}
              {/* Orice alt rut, inclusiv rdcina, redirecioneaz la login dac nu e logat */}
              <Route path="*" element={<Navigate to="/login" replace />} />
            </>
          )}
        </Routes>
      </div>
    </>
  );
};

export default App;
