/**
 * Hooks personalizate pentru interogri legate de tranzacii
 * Utilizeaz utilitarele avansate React Query pentru optimizri de performan
 */

import { useQuery, useInfiniteQuery, useQueryClient } from '@tanstack/react-query';
import { 
  createQueryKeyFactory, 
  getInfiniteQueryOptions,
  cacheConfig
} from '../reactQueryUtils';
import type { Transaction, TransactionFilters } from '../../types/Transaction';
import { transactionService } from '../transactionService';
import { useMemo } from 'react';
import { EXCEL_GRID } from '@shared-constants';

// Adaptm interfaa TransactionFilters pentru a fi compatibil cu Record<string, unknown>
type TransactionFiltersRecord = TransactionFilters & Record<string, unknown>;

// Crem factory de chei pentru query-urile legate de tranzacii
export const transactionKeys = createQueryKeyFactory('transactions');

/**
 * Hook pentru a obine o singur tranzacie dup ID
 */
export function useTransaction(id: string | number) {
  return useQuery({
    queryKey: transactionKeys.detail(id),
    queryFn: () => transactionService.getTransactionById(id),
    staleTime: cacheConfig.expirationTimes.transactions,
    enabled: !!id,
  });
}

/**
 * Hook pentru a obine lista de tranzacii filtrate
 */
export function useTransactions(filters?: TransactionFilters) {
  return useQuery({
    queryKey: transactionKeys.list(filters as TransactionFiltersRecord),
    queryFn: () => transactionService.getTransactions(filters),
    staleTime: cacheConfig.expirationTimes.transactions,
    // Optimizare pentru a preveni flicker-ul UI la schimbarea filtrelor
    // keepPreviousData este deprecated, folosim placeholderData pentru acelai efect
    placeholderData: keepPreviousData => keepPreviousData
  });
}

/**
 * Hook pentru infinite loading de tranzacii cu optimizri
 */
export function useInfiniteTransactions(
  filters?: TransactionFilters,
  pageSize: number = 20
) {
  return useInfiniteQuery({
    queryKey: transactionKeys.infinite(filters as TransactionFiltersRecord),
    queryFn: ({ pageParam = 1 }) => 
      transactionService.getTransactionsPaginated({
        ...filters,
        page: pageParam,
        pageSize,
      }),
    initialPageParam: 1, // Necesar n noile versiuni de React Query
    ...getInfiniteQueryOptions({
      staleTime: cacheConfig.expirationTimes.transactions,
      maxPages: 5, // Limitm numrul de pagini reinute n memorie
    }),
  });
}

/**
 * Hook optimizat pentru obinerea tranzaciilor lunare pentru grid
 * Utilizeaz memorare intern pentru prevenirea recalculrilor
 */
export function useMonthlyTransactions(
  month: number,
  year: number,
  additionalFilters?: Omit<TransactionFilters, 'month' | 'year'>
) {
  const queryClient = useQueryClient();
  
  // Crem filtrele pentru query
  const filters = useMemo(() => ({
    month,
    year,
    ...additionalFilters
  }), [month, year, additionalFilters]);
  
  // Interogm tranzaciile folosind filtrele combinate
  const query = useQuery({
    queryKey: transactionKeys.custom('monthly', { month, year, ...additionalFilters }),
    queryFn: () => transactionService.getMonthlyTransactions(filters),
    staleTime: cacheConfig.expirationTimes.transactions,
    // keepPreviousData este deprecated, folosim placeholderData pentru acelai efect
    placeholderData: keepPreviousData => keepPreviousData
  });
  
  // Procesm datele pentru a le grupa pe categorii i subcategorii
  // Memorm rezultatele pentru a preveni recalculri inutile
  const groupedTransactions = useMemo(() => {
    if (!query.data) return { byCategory: {}, bySubcategory: {} };
    
    // Grupm dup categorie
    const byCategory: Record<string, Transaction[]> = {};
    
    // Grupm dup subcategorie
    const bySubcategory: Record<string, Record<string, Transaction[]>> = {};
    
    // Verificm dac avem date i iterm o singur dat pentru eficien
    const transactions = Array.isArray(query.data) ? query.data : [];
    transactions.forEach(transaction => {
      const { category, subcategory } = transaction;
      
      // Adugm la grupul categoriei
      if (!byCategory[category]) {
        byCategory[category] = [];
      }
      byCategory[category].push(transaction);
      
      // Adugm la grupul subcategoriei
      if (!bySubcategory[category]) {
        bySubcategory[category] = {};
      }
      if (!bySubcategory[category][subcategory]) {
        bySubcategory[category][subcategory] = [];
      }
      bySubcategory[category][subcategory].push(transaction);
    });
    
    return { byCategory, bySubcategory };
  }, [query.data]);
  
  // Prefetch pentru lunile adiacente pentru experien mbuntit
  const prefetchAdjacentMonths = () => {
    // Prefetch luna urmtoare
    let nextMonth = month + 1;
    let nextYear = year;
    if (nextMonth > 12) {
      nextMonth = 1;
      nextYear += 1;
    }
    
    // Prefetch luna anterioar
    let prevMonth = month - 1;
    let prevYear = year;
    if (prevMonth < 1) {
      prevMonth = 12;
      prevYear -= 1;
    }
    
    // Prefetch n background pentru lunile adiacente
    queryClient.prefetchQuery({
      queryKey: transactionKeys.custom('monthly', { month: nextMonth, year: nextYear }),
      queryFn: () => transactionService.getMonthlyTransactions({ 
        ...additionalFilters, 
        month: nextMonth, 
        year: nextYear 
      }),
      staleTime: cacheConfig.expirationTimes.transactions,
    });
    
    queryClient.prefetchQuery({
      queryKey: transactionKeys.custom('monthly', { month: prevMonth, year: prevYear }),
      queryFn: () => transactionService.getMonthlyTransactions({ 
        ...additionalFilters, 
        month: prevMonth, 
        year: prevYear 
      }),
      staleTime: cacheConfig.expirationTimes.transactions,
    });
  };
  
  // Calculm totaluri pentru categorii i subcategorii
  const totals = useMemo(() => {
    const categoryTotals: Record<string, number> = {};
    const subcategoryTotals: Record<string, Record<string, number>> = {};
    let monthTotal = 0;
    
    // Calculm totalurile pentru categorii
    Object.keys(groupedTransactions.byCategory).forEach(category => {
      const transactions = groupedTransactions.byCategory[category];
      const total = transactions.reduce((sum, t) => sum + parseFloat(t.amount.toString()), 0);
      categoryTotals[category] = total;
      monthTotal += total;
      
      // Calculm totalurile pentru subcategorii
      subcategoryTotals[category] = {};
      if (groupedTransactions.bySubcategory[category]) {
        Object.keys(groupedTransactions.bySubcategory[category]).forEach(subcategory => {
          const subTransactions = groupedTransactions.bySubcategory[category][subcategory];
          subcategoryTotals[category][subcategory] = subTransactions.reduce(
            (sum, t) => sum + parseFloat(t.amount.toString()), 
            0
          );
        });
      }
    });
    
    return { categoryTotals, subcategoryTotals, monthTotal };
  }, [groupedTransactions]);
  
  // Construim datele pentru grid n formatul ateptat
  const gridData = useMemo(() => {
    const days = EXCEL_GRID.DAYS_IN_MONTH[month - 1];
    const dailyTransactions: Record<number, Transaction[]> = {};
    
    // Iniializm array-ul pentru fiecare zi
    for (let day = 1; day <= days; day++) {
      dailyTransactions[day] = [];
    }
    
    // Populm tranzaciile pentru fiecare zi
    if (query.data) {
      const transactions = Array.isArray(query.data) ? query.data : [];
      transactions.forEach(transaction => {
        const day = new Date(transaction.date).getDate();
        if (dailyTransactions[day]) {
          dailyTransactions[day].push(transaction);
        }
      });
    }
    
    return dailyTransactions;
  }, [query.data, month]);
  
  return {
    ...query,
    groupedTransactions,
    gridData,
    totals,
    prefetchAdjacentMonths,
  };
} 