import React from 'react';
import Button, { ButtonProps } from './Button';
import { BUTTONS } from '@shared-constants';

export interface ValidatedSubmitButtonProps extends Omit<ButtonProps, 'variant'> {
  /**
   * Indic dac formularul este valid i poate fi trimis
   */
  isFormValid: boolean;
  
  /**
   * Indic dac operaia este n curs (loading)
   * Convertm orice valoare non-falsy la boolean pentru compatibilitate
   */
  isLoading?: any;
  
  /**
   * Text pentru buton n stare de loading (opional)
   */
  loadingText?: string;
  
  /**
   * Text pentru buton n stare normal (opional)
   */
  submitText?: string;
}

/**
 * Buton specializat pentru formulare, cu stare validat
 * Schimb automat varianta i efectele vizuale n funcie de validitatea formularului
 * 
 * @param props - Proprietile butonului
 * @returns Un buton adaptat pentru submit cu stare validat
 */
const ValidatedSubmitButton: React.FC<ValidatedSubmitButtonProps> = ({
  isFormValid,
  isLoading = false,
  disabled = false,
  loadingText = BUTTONS.LOADING,
  submitText,
  children,
  ...rest
}) => {
  return (
    <Button
      type="submit"
      variant={isFormValid ? 'success' : 'primary'} 
      disabled={!isFormValid || isLoading || disabled}
      isLoading={Boolean(isLoading)}
      withShadow={Boolean(isFormValid)}
      withGradient={Boolean(isFormValid)}
      withTranslate={Boolean(isFormValid)}
      {...rest}
    >
      {isLoading ? loadingText : (submitText || children)}
    </Button>
  );
};

export default ValidatedSubmitButton;
