// Schem i tipuri partajate pentru tranzacii
import { z } from 'zod';
import { TransactionType, FrequencyType, TransactionStatus } from './enums';

// Diferene fa de structura SQL:
// - currency NU este folosit n FE (intenionat, vezi discuie cu owner)
// - description exist doar n FE (nu i n SQL, dar poate fi adugat la nevoie)
// - user_id nu e expus n FE (doar pe backend)
// - created_at/updated_at sunt opionale, folosite doar pentru UI/audit

export const TransactionSchema = z.object({
  id: z.string().uuid(),
  amount: z.number(),
  type: z.nativeEnum(TransactionType), // Enum strict pentru tip tranzacie
  date: z.string(),
  description: z.string().optional(),
  category: z.string().optional(),
  subcategory: z.string().optional(),
  recurring: z.boolean().optional(),
  frequency: z.nativeEnum(FrequencyType).optional(), // Enum strict pentru frecven
  // --- cmpuri pentru workflow planificat/actual ---
  actualAmount: z.number().optional(), // Suma efectiv cheltuit (dac difer de estimat)
  status: z.nativeEnum(TransactionStatus).optional(), // 'PLANNED' sau 'COMPLETED'
  // --- meta ---
  created_at: z.string().optional(), // ISO date
  updated_at: z.string().optional(), // ISO date
});

// Schema pentru CREATE (fr id, created_at, updated_at)
export const CreateTransactionSchema = TransactionSchema.omit({ id: true, created_at: true, updated_at: true });

export type TransactionValidated = z.infer<typeof TransactionSchema>;
export type CreateTransaction = z.infer<typeof CreateTransactionSchema>;
