import { useQuery } from '@tanstack/react-query';
import { supabaseService, ActiveSubcategory } from '../supabaseService';
import { useAuthStore } from '../../stores/authStore';
import { useMemo } from 'react';

export interface UseActiveSubcategoriesParams {
  category?: string;
  type?: string;
  enabled?: boolean;
}

/**
 * Hook pentru obinerea subcategoriilor active (care au cel puin o tranzacie)
 * 
 * Folosete React Query pentru caching i revalidare automat
 * Returneaz subcategoriile care au tranzacii asociate, pentru a afia
 * doar opiuni relevante n filtre
 * 
 * @param params Parametri opionali (category, type) pentru filtrare
 * @returns Rezultatul query-ului cu subcategoriile active i metadate
 */
export function useActiveSubcategories({
  category,
  type,
  enabled = true
}: UseActiveSubcategoriesParams = {}) {
  // Obinem userId din store-ul de autentificare
  const { user } = useAuthStore();
  const userId = user?.id;
  
  // Definim cheia de query pentru React Query
  const queryKey = ['activeSubcategories', userId, category, type];
  
  // Executm query-ul pentru subcategoriile active
  const query = useQuery<ActiveSubcategory[], Error>({
    queryKey,
    queryFn: async () => {
      return supabaseService.fetchActiveSubcategories(userId, category, type);
    },
    gcTime: 5 * 60 * 1000, // 5 minute
    staleTime: 30 * 1000,  // 30 secunde
    enabled: enabled && !!userId, // Activm query-ul doar dac userId este disponibil i enabled=true
  });
  
  // Memoizm lista formatat a subcategoriilor
  const formattedSubcategories = useMemo(() => {
    if (!query.data) return [];
    
    // Transformm datele n formatul necesar pentru componenta Select
    return query.data.map(item => ({
      value: item.subcategory,
      label: item.subcategory.charAt(0) + item.subcategory.slice(1).toLowerCase().replace(/_/g, ' ') + 
             ` (${item.count})`, // Adugm numrul de tranzacii ntre paranteze
      count: item.count,
      category: item.category
    }));
  }, [query.data]);
  
  // Filtrm doar subcategoriile din categoria selectat (dac exist una)
  const filteredSubcategories = useMemo(() => {
    if (!category) return formattedSubcategories;
    
    return formattedSubcategories.filter(item => item.category === category);
  }, [formattedSubcategories, category]);
  
  return {
    subcategories: filteredSubcategories,
    isLoading: query.isLoading,
    isError: query.isError,
    error: query.error,
    refetch: query.refetch,
    // Adugm informaii utile pentru componente
    isEmpty: query.data?.length === 0,
    isSuccess: query.isSuccess
  };
} 