// Serviciu pentru gestionarea categoriilor i subcategoriilor personalizate (CRUD + utilitare)
// Owner: echipa FE
import { supabase } from './supabase';
import { CustomCategoriesPayload } from '../types/Category';
import { API } from '@shared-constants/api';
import { MESAJE } from '@shared-constants/messages';

// Helper pentru logging consistent
const logError = (context: string, error: any) => {
  console.error(`[categoryService] ${context}:`, error);
};

// Tabelele ar trebui mutate n API.TABLES conform regulilor globale (memoria 886c7659)
// TODO: A se muta n shared-constants/api.ts dup ce acest serviciu este stabil
const TABLE = 'custom_categories';

// Schema tabel custom_categories pe care o expectm n Supabase
// user_id (primary key, string)
// category_data (jsonb) - conine { categories: CustomCategory[], version: number }
// updated_at (timestamp)
// version (integer, optional)

export const categoryService = {
  async getUserCategories(userId: string): Promise<CustomCategoriesPayload | null> {
    if (!userId) {
      return { categories: [], version: 1 }; // Cazul demo sau neautentificat
    }
    
    try {
      // Evitm folosirea .single() care arunc eroare dac nu gsete date
      const { data, error } = await supabase
        .from(TABLE)
        .select('category_data, version')
        .eq('user_id', userId);
      
      if (error) {
        logError('getUserCategories', error);
        return { categories: [], version: 1 }; // Valoare implicit sigur
      }
      
      // Dac nu exist date sau array-ul e gol, returneaz categorii goale
      if (!data || data.length === 0) {
        return { categories: [], version: 1 };
      }
      
      // Avem date - folosim primul rezultat 
      const userCategories = data[0];
      
      // Validare pentru a ne asigura c avem structura ateptat
      if (!userCategories.category_data || typeof userCategories.category_data !== 'object') {
        return { categories: [], version: 1 };
      }
      
      // Ne asigurm c avem array de categorii valid
      const categories = Array.isArray(userCategories.category_data.categories) 
        ? userCategories.category_data.categories 
        : [];
      
      const version = typeof userCategories.version === 'number' 
        ? userCategories.version 
        : (userCategories.category_data.version || 1);
      
      return { 
        categories,
        version
      };
    } catch (err) {
      logError('getUserCategories exception', err);
      return { categories: [], version: 1 }; // Valoare implicit sigur n caz de excepie
    }
  },

  async saveUserCategories(userId: string, categoryData: CustomCategoriesPayload): Promise<boolean> {
    try {
      // Validm datele nainte de a le trimite
      if (!userId) {
        logError('saveUserCategories', 'User ID este obligatoriu');
        return false;
      }
      
      // Ne asigurm c avem obiect valid
      if (!categoryData || typeof categoryData !== 'object') {
        logError('saveUserCategories', 'Datele de categorie lipsesc sau sunt invalide');
        return false;
      }
      
      // Ne asigurm c categories este array
      const categories = Array.isArray(categoryData.categories) 
        ? categoryData.categories 
        : [];
      
      // FORMAT CORECT TESTAT: Construim un obiect exact n structura ateptat de Supabase
      // Baza de date ateapt: 
      // 1. user_id = uuid utilizator
      // 2. category_data = jsonb cu format {categories: Array, version: Number}
      //    ATENIE: Pentru jsonb, nu trebuie s stringifym manual, ci s trimitem obiectul ca atare
      // 3. version = integer (nu n category_data)
      // 
      // Vezi analiza MCP pe tabela custom_categories:
      // {id, user_id, category_data (jsonb), updated_at, version}
      const currentVersion = typeof categoryData.version === 'number' ? categoryData.version : 1;
      
      // ATENIE: Nu includem id, deoarece este generat automat de Supabase cu gen_random_uuid()
      const payload = {
        user_id: userId,
        // IMPORTANT: Supabase accept direct obiectul pentru cmpurile jsonb,
        // dar trebuie s fie un obiect valid cu structure {categories, version}
        category_data: { 
          categories,
          version: currentVersion 
        },
        updated_at: new Date().toISOString(),
        version: currentVersion
      };
      
      // Logare pentru debugging - verificm format
      console.debug('[categoryService] Payload trimis:', payload);
      
      // Pentru upsert trebui s verificm nti dac exist deja o nregistrare pentru utilizator
      const { data: existingData, error: selectError } = await supabase
        .from(TABLE)
        .select('id')
        .eq('user_id', userId);
      
      if (selectError) {
        logError('saveUserCategories select', selectError);
        return false;
      }
      
      // Determinm dac trebuie s facem insert sau update
      if (!existingData || existingData.length === 0) {
        // Insert - prima dat cnd salvm categorii pentru acest utilizator
        const { error: insertError } = await supabase
          .from(TABLE)
          .insert([payload]);
          
        if (insertError) {
          logError('saveUserCategories insert', insertError);
          return false;
        }
      } else {
        // Update - actualizm categoriile existente folosind id-ul existent
        const { error: updateError } = await supabase
          .from(TABLE)
          .update(payload)
          .eq('user_id', userId);
          
        if (updateError) {
          logError('saveUserCategories update', updateError);
          return false;
        }
      }
      
      // Dac am ajuns aici, nseamn c totul a mers bine
      
      return true;
    } catch (err) {
      logError('saveUserCategories exception', err);
      return false;
    }
  },

  async updateTransactionsForSubcategoryRename(userId: string, category: string, oldName: string, newName: string): Promise<boolean> {
    try {
      // Validm parametrii
      if (!userId) {
        logError('updateTransactionsForSubcategoryRename', 'User ID este obligatoriu');
        return false;
      }
      if (!category || !oldName || !newName) {
        logError('updateTransactionsForSubcategoryRename', 'Categoria, numele vechi i numele nou sunt obligatorii');
        return false;
      }
      
      // Update n batch pentru tranzaciile cu subcategoria veche
      // Folosim numele explicit al tabelului n loc de string hardcodat
      const { error } = await supabase
        .from('transactions') // Eventual mutare n API.TABLES
        .update({ subcategory: newName.trim() })
        .eq('user_id', userId)
        .eq('category', category)
        .eq('subcategory', oldName);
      
      if (error) {
        logError('updateTransactionsForSubcategoryRename', error);
        return false;
      }
      return true;
    } catch (err) {
      logError('updateTransactionsForSubcategoryRename exception', err);
      return false;
    }
  },

  async handleSubcategoryDeletion(userId: string, category: string, subcategory: string, action: 'migrate' | 'delete', target?: string): Promise<boolean> {
    try {
      // Validm parametrii
      if (!userId) {
        logError('handleSubcategoryDeletion', 'User ID este obligatoriu');
        return false;
      }
      if (!category || !subcategory) {
        logError('handleSubcategoryDeletion', 'Categoria i subcategoria sunt obligatorii');
        return false;
      }
      
      if (action === 'migrate' && target) {
        // Migrare tranzacii ctre alt subcategorie
        const { error } = await supabase
          .from('transactions') // Eventual mutare n API.TABLES
          .update({ subcategory: target.trim() })
          .eq('user_id', userId)
          .eq('category', category)
          .eq('subcategory', subcategory);
          
        if (error) {
          logError('handleSubcategoryDeletion - migrate', error);
          return false;
        }
        return true;
      } 
      else if (action === 'delete') {
        // n loc s tergem tranzaciile, mutm la categoria principal (subcategorie null)
        // Aceasta este mai sigur dect tergerea complet a tranzaciilor
        const { error } = await supabase
          .from('transactions') // Eventual mutare n API.TABLES
          .update({ subcategory: null })
          .eq('user_id', userId)
          .eq('category', category)
          .eq('subcategory', subcategory);
          
        if (error) {
          logError('handleSubcategoryDeletion - delete', error);
          return false;
        }
        return true;
      }
      
      logError('handleSubcategoryDeletion', `Action necunoscut: ${action}`);
      return false;
    } catch (err) {
      logError('handleSubcategoryDeletion exception', err);
      return false;
    }
  },

  async getAvailableSubcategoriesForMigration(userId: string, category: string, excludeSubcategory: string): Promise<string[]> {
    try {
      // Validm parametrii
      if (!userId || !category || !excludeSubcategory) {
        logError('getAvailableSubcategoriesForMigration', 'Parametri insuficieni');
        return [];
      }
      
      // Extrage subcategoriile disponibile pentru migrare (excluznd pe cea tears)
      const userCats = await this.getUserCategories(userId);
      if (!userCats || !Array.isArray(userCats.categories)) {
        return [];
      }
      
      const cat = userCats.categories.find(c => c.name === category);
      if (!cat || !Array.isArray(cat.subcategories)) {
        return [];
      }
      
      // Filtreaz subcategoriile valide, asigur string-uri curate
      return cat.subcategories
        .filter(sc => !!sc && typeof sc.name === 'string')
        .map(sc => sc.name)
        .filter(name => name !== excludeSubcategory);
    } catch (err) {
      logError('getAvailableSubcategoriesForMigration exception', err);
      return [];
    }
  },

  async checkForDuplicateSubcategory(userId: string, category: string, subcategoryName: string): Promise<boolean> {
    try {
      // Validm parametrii
      if (!userId || !category || !subcategoryName) {
        return false; // Nu e duplicat dac parametrii sunt invalizi
      }
      
      // Curm numele subcategoriei pentru comparaie
      const cleanName = subcategoryName.trim().toLowerCase();
      if (!cleanName) {
        return false; // Nume gol, nu poate fi duplicat
      }
      
      // Verific dac exist deja o subcategorie cu acest nume n categoria dat
      const userCats = await this.getUserCategories(userId);
      if (!userCats || !Array.isArray(userCats.categories)) {
        return false;
      }
      
      const cat = userCats.categories.find(c => c.name === category);
      if (!cat || !Array.isArray(cat.subcategories)) {
        return false;
      }
      
      // Verific dac exist subcategorie cu acelai nume, ignornd case i spaii
      return cat.subcategories.some(sc => 
        sc && typeof sc.name === 'string' && 
        sc.name.trim().toLowerCase() === cleanName
      );
    } catch (err) {
      logError('checkForDuplicateSubcategory exception', err);
      return false; // n caz de eroare, presupunem c nu e duplicat
    }
  }
};
