import { useInfiniteQuery } from '@tanstack/react-query';
import { supabaseService } from '../supabaseService';
import type { TransactionPage, Pagination } from '../supabaseService';
import type { TransactionValidated } from '@shared-constants/transaction.schema';
import { TransactionType } from '@shared-constants/enums';
import { useAuthStore } from '../../stores/authStore';
import type { Transaction } from '../../types/Transaction';
import { useMemo } from 'react';

export interface TransactionQueryParams {
  limit?: number;
  offset?: number;
  type?: TransactionType | string;
  category?: string;
  subcategory?: string;
  recurring?: boolean;
  startDate?: string;
  endDate?: string;
  sort?: string;
  order?: 'asc' | 'desc';
  minAmount?: number;
  maxAmount?: number;
  search?: string;
}

export interface UseInfiniteTransactionsResult {
  data: (TransactionValidated & { userId?: string })[];
  pages: TransactionPage[];
  isLoading: boolean;
  isFetching: boolean;
  error: Error | null;
  hasNextPage: boolean;
  fetchNextPage: () => void;
  isFetchingNextPage: boolean;
  totalCount: number;
}

/**
 * Hook specializat pentru ncrcarea tranzaciilor cu paginare infinit
 * Folosete useInfiniteQuery pentru a ncrca date pe msur ce utilizatorul deruleaz
 * 
 * @param queryParams Parametri pentru filtrare i paginare
 * @returns Rezultatul query-ului cu tranzacii, status i funcii de control pentru paginare
 */
export function useInfiniteTransactions(
  queryParams: TransactionQueryParams
): UseInfiniteTransactionsResult {
  // Obinem userId din store
  const { user } = useAuthStore();
  const userId = user?.id;
  
  // Definim dimensiunea paginii pentru ncrcarea pagin cu pagin
  const PAGE_SIZE = queryParams.limit || 10;
  
  // Folosim aceeai cheie de baz ca n transactionMutations.ts pentru a asigura
  // invalidarea corect a cache-ului la mutaii
  const TRANSACTIONS_BASE_KEY = ['transactions'] as const;
  
  // Construim cheia de query extins pentru acest hook specific
  const queryKey = [...TRANSACTIONS_BASE_KEY, 'infinite', userId, queryParams];
  
  // Folosim useInfiniteQuery pentru paginare infinit
  const infiniteQuery = useInfiniteQuery<TransactionPage, Error>({
    queryKey,
    initialPageParam: 0, // Parametrul iniial pentru prima pagin (offset 0)
    queryFn: async ({ pageParam }) => {
      // Actualizm offset-ul bazat pe pageParam pentru paginare infinit
      const pagination: Pagination = {
        limit: PAGE_SIZE,
        offset: pageParam as number,
        sort: queryParams.sort as 'date' | 'amount' | 'created_at' | undefined,
        order: queryParams.order,
      };
      
      const filters = {
        type: queryParams.type as TransactionType | undefined,
        category: queryParams.category,
        subcategory: queryParams.subcategory,
        recurring: queryParams.recurring,
        dateFrom: queryParams.startDate,
        dateTo: queryParams.endDate,
        minAmount: queryParams.minAmount,
        maxAmount: queryParams.maxAmount,
        search: queryParams.search
      };
      
      const result = await supabaseService.fetchTransactions(userId, pagination, filters);
      
      // Nu modificm tipul datelor returnate din queryFn pentru a evita erorile TypeScript
      return result;
    },
    // Funcia getNextPageParam determin dac mai exist pagini de ncrcat i care este urmtorul pageParam
    getNextPageParam: (lastPage, allPages) => {
      // Calculm offset-ul curent bazat pe paginile existente i dimensiunea paginii
      const currentOffset = allPages.length * PAGE_SIZE;
      
      // Dac numrul de rezultate din ultima pagin este mai mic dect dimensiunea paginii,
      // atunci nu mai sunt pagini disponibile
      if (lastPage.data.length < PAGE_SIZE) {
        return undefined; // Nu mai sunt pagini disponibile
      }

      // Dac am recuperat toate nregistrrile conform count-ului total, nu mai sunt pagini disponibile
      if (currentOffset >= lastPage.count) {
        return undefined;
      }

      // Altfel, returnam urmtorul offset ca pageParam
      return currentOffset;
    },
    // Configurri pentru comportamentul cache-ului
    gcTime: 5 * 60 * 1000, // 5 minute
    staleTime: 30 * 1000, // 30 secunde
    refetchOnWindowFocus: true,
    refetchOnMount: true,
    refetchOnReconnect: true,
    retry: 3,
    enabled: !!userId, // Activm query-ul doar dac userId este disponibil
    // TODO: Pentru UX avansat, poi implementa manual pstrarea datelor vechi aici
    // placeholderData: undefined,
  });
  
  // Procesm datele pentru a le face compatibile cu interfaa Transaction
  const processedTransactions = useMemo(() => {
    const allTransactions = infiniteQuery.data?.pages.flatMap(page => page.data) || [];
    
    // Adugm userId i id (dac este necesar) la fiecare tranzacie
    return allTransactions.map(transaction => {
      // Folosim tipul any temporar pentru a evita erorile TypeScript
      const anyTransaction = transaction as any;
      
      // Adugm userId dac nu exist
      if (!anyTransaction.userId && userId) {
        anyTransaction.userId = userId;
      }
      
      // Adugm id bazat pe _id dac lipsete
      if (anyTransaction._id && !anyTransaction.id) {
        anyTransaction.id = anyTransaction._id;
      }
      
      return anyTransaction;
    });
  }, [infiniteQuery.data, userId]);
  
  // Calculm numrul total de tranzacii din rezultatele paginii
  const totalCount = infiniteQuery.data?.pages[0]?.count || 0;
  
  return {
    data: processedTransactions,
    pages: infiniteQuery.data?.pages || [],
    isLoading: infiniteQuery.isLoading,
    isFetching: infiniteQuery.isFetching,
    error: infiniteQuery.error,
    hasNextPage: infiniteQuery.hasNextPage || false,
    fetchNextPage: infiniteQuery.fetchNextPage,
    isFetchingNextPage: infiniteQuery.isFetchingNextPage,
    totalCount,
  };
}
