/**
 * Serviciu optimizat pentru gestionarea tranzaciilor
 * 
 * Acest serviciu utilizeaz utilitarele de performan pentru a optimiza
 * interogrile, mutaiile i managementul cache-ului pentru tranzacii.
 */

import { API } from '@shared-constants/api';
import { Transaction, TransactionFilters } from '../types/Transaction';
import { createCachedQueryFn } from './reactQueryUtils';
import { memoizeRequest } from '../utils/performanceUtils';

// Tip pentru rspunsul paginat
interface PaginatedResponse<T> {
  data: T[];
  total: number;
  page: number;
  pageSize: number;
  hasMore: boolean;
  nextCursor?: string | number;
}

/**
 * Construiete URL-ul pentru API cu parametrii de interogare
 */
function buildQueryUrl(endpoint: string, params?: Record<string, any>): string {
  const url = new URL(endpoint, window.location.origin);
  
  if (params) {
    Object.entries(params).forEach(([key, value]) => {
      if (value !== undefined && value !== null) {
        url.searchParams.append(key, String(value));
      }
    });
  }
  
  return url.toString();
}

/**
 * Serviciu pentru tranzacii cu optimizri de performan
 */
class TransactionService {
  /**
   * Obine o tranzacie dup ID
   * Memorm rezultatul pentru a evita cereri redundante
   */
  getTransactionById = memoizeRequest(
    async (id: string | number): Promise<Transaction> => {
      const response = await fetch(`${API.ROUTES.TRANSACTIONS}/${id}`);
      
      if (!response.ok) {
        throw new Error(`Eroare la obinerea tranzaciei: ${response.statusText}`);
      }
      
      return response.json();
    },
    {
      maxAge: 5 * 60 * 1000, // 5 minute
      keyGenerator: (id) => `transaction:${id}`,
    }
  );
  
  /**
   * Obine lista de tranzacii cu filtre opionale
   * Utilizeaz cache pentru a optimiza cereri repetate
   */
  getTransactions = createCachedQueryFn(
    async (filters?: TransactionFilters): Promise<Transaction[]> => {
      const queryUrl = buildQueryUrl(API.ROUTES.TRANSACTIONS, filters);
      const response = await fetch(queryUrl);
      
      if (!response.ok) {
        throw new Error(`Eroare la obinerea tranzaciilor: ${response.statusText}`);
      }
      
      return response.json();
    },
    {
      cacheKey: (filters) => `transactions:list:${JSON.stringify(filters || {})}`,
      maxAge: 5 * 60 * 1000, // 5 minute
    }
  );
  
  /**
   * Obine tranzacii paginate pentru infinite loading
   */
  getTransactionsPaginated = async (
    params: TransactionFilters
  ): Promise<PaginatedResponse<Transaction>> => {
    const queryUrl = buildQueryUrl(API.ROUTES.TRANSACTIONS, {
      ...params,
      paginated: true,
    });
    
    const response = await fetch(queryUrl);
    
    if (!response.ok) {
      throw new Error(`Eroare la obinerea tranzaciilor paginate: ${response.statusText}`);
    }
    
    return response.json();
  };
  
  /**
   * Obine tranzaciile pentru o lun specific
   * Optimizat pentru vizualizarea lunar
   */
  getMonthlyTransactions = createCachedQueryFn(
    async (filters: { month: number; year: number } & Omit<TransactionFilters, 'month' | 'year'>): Promise<Transaction[]> => {
      const { month, year, ...otherFilters } = filters;
      
      // Calculm data de nceput i sfrit pentru luna specificat
      const startDate = new Date(year, month - 1, 1);
      const endDate = new Date(year, month, 0); // Ultima zi a lunii
      
      const queryUrl = buildQueryUrl(API.ROUTES.TRANSACTIONS, {
        ...otherFilters,
        startDate: startDate.toISOString().split('T')[0],
        endDate: endDate.toISOString().split('T')[0],
        includeAdjacentDays: filters.includeAdjacentDays || false,
      });
      
      const response = await fetch(queryUrl);
      
      if (!response.ok) {
        throw new Error(`Eroare la obinerea tranzaciilor lunare: ${response.statusText}`);
      }
      
      return response.json();
    },
    {
      cacheKey: (filters) => `transactions:monthly:${filters.month}:${filters.year}:${JSON.stringify(filters)}`,
      maxAge: 5 * 60 * 1000, // 5 minute
    }
  );
  
  /**
   * Creeaz o nou tranzacie
   */
  createTransaction = async (transaction: Omit<Transaction, 'id'>): Promise<Transaction> => {
    const response = await fetch(API.ROUTES.TRANSACTIONS, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(transaction),
    });
    
    if (!response.ok) {
      throw new Error(`Eroare la crearea tranzaciei: ${response.statusText}`);
    }
    
    return response.json();
  };
  
  /**
   * Actualizeaz o tranzacie existent
   */
  updateTransaction = async (id: string | number, transaction: Partial<Transaction>): Promise<Transaction> => {
    const response = await fetch(`${API.ROUTES.TRANSACTIONS}/${id}`, {
      method: 'PATCH',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(transaction),
    });
    
    if (!response.ok) {
      throw new Error(`Eroare la actualizarea tranzaciei: ${response.statusText}`);
    }
    
    return response.json();
  };
  
  /**
   * terge o tranzacie
   */
  deleteTransaction = async (id: string | number): Promise<void> => {
    const response = await fetch(`${API.ROUTES.TRANSACTIONS}/${id}`, {
      method: 'DELETE',
    });
    
    if (!response.ok) {
      throw new Error(`Eroare la tergerea tranzaciei: ${response.statusText}`);
    }
  };
  
  /**
   * Export tranzaciile n format CSV sau Excel
   */
  exportTransactions = async (filters: TransactionFilters, format: 'csv' | 'excel' = 'csv'): Promise<Blob> => {
    const queryUrl = buildQueryUrl(`${API.ROUTES.TRANSACTIONS}/export`, {
      ...filters,
      format,
    });
    
    const response = await fetch(queryUrl);
    
    if (!response.ok) {
      throw new Error(`Eroare la exportul tranzaciilor: ${response.statusText}`);
    }
    
    return response.blob();
  };
  
  /**
   * Import tranzacii din fiier
   */
  importTransactions = async (file: File): Promise<{ total: number; imported: number; errors: any[] }> => {
    const formData = new FormData();
    formData.append('file', file);
    
    const response = await fetch(`${API.ROUTES.TRANSACTIONS}/import`, {
      method: 'POST',
      body: formData,
    });
    
    if (!response.ok) {
      throw new Error(`Eroare la importul tranzaciilor: ${response.statusText}`);
    }
    
    return response.json();
  };
}

// Exportm o instan singleton
export const transactionService = new TransactionService();

// Exportm tipul serviciului pentru DI i mock-uri
export type ITransactionService = TransactionService; 