import { createClient, SupabaseClient } from '@supabase/supabase-js';

// Se ncearc citirea din variabile de mediu (vite sau CRA)
// Setup corect pentru Create React App (CRA):
// Seteaz valorile n .env.local ca REACT_APP_SUPABASE_URL i REACT_APP_SUPABASE_ANON_KEY
// Nu comita aceste valori n repo public!
const supabaseUrl = process.env.REACT_APP_SUPABASE_URL || '';
const supabaseAnonKey = process.env.REACT_APP_SUPABASE_ANON_KEY || '';

if (!supabaseUrl || !supabaseAnonKey) {
  // Mesaj clar pentru debugging/configurare CI
  // Nu arunca eroare hard, dar logheaz vizibil
  // (poate fi util pentru testare/mocking)
  // eslint-disable-next-line no-console
  console.error('[Supabase] Configurare lips: seteaz VITE_SUPABASE_URL i VITE_SUPABASE_ANON_KEY n .env.local');
}

export const supabase: SupabaseClient = createClient(supabaseUrl, supabaseAnonKey);
