import React, { useState, useCallback, useEffect, useMemo } from 'react';
import Button from '../../primitives/Button/Button';
import Select from '../../primitives/Select/Select';
import Badge from '../../primitives/Badge/Badge';
import Input from '../../primitives/Input/Input';
import { OPTIONS, LABELS, PLACEHOLDERS } from '@shared-constants';
import type { TransactionType, CategoryType } from '@shared-constants';
import { useCategoryStore } from '../../../stores/categoryStore';
import { useThemeEffects } from '../../../hooks';
import { BUTTONS, UI, TABLE, LOADER, INFO } from '@shared-constants/ui';
import classNames from 'classnames';
import { useActiveSubcategories } from '../../../services/hooks/useActiveSubcategories';
import { MESAJE } from '@shared-constants/messages';

export interface TransactionFiltersProps {
  type?: TransactionType | '' | string;
  category?: CategoryType | '' | string;
  subcategory?: string;
  onTypeChange?: (type: TransactionType | '' | string) => void;
  onCategoryChange?: (category: CategoryType | '' | string) => void;
  onSubcategoryChange?: (subcategory: string) => void;
  dateFrom?: string;
  dateTo?: string;
  amountMin?: string;
  amountMax?: string;
  searchText?: string;
  onDateFromChange?: (dateFrom: string) => void;
  onDateToChange?: (dateTo: string) => void;
  onAmountMinChange?: (amountMin: string) => void;
  onAmountMaxChange?: (amountMax: string) => void;
  onSearchTextChange?: (searchText: string) => void;
}

/**
 * Custom hook pentru debounce
 * Permite reducerea numrului de apeluri pentru text input
 */
function useDebounce<T>(value: T, delay: number): T {
  // State i setter pentru valoarea debounced
  const [debouncedValue, setDebouncedValue] = useState<T>(value);
  
  useEffect(
    () => {
      // Actualizm valoarea debouncedValue dup delay
      const handler = setTimeout(() => {
        setDebouncedValue(value);
      }, delay);
      
      // Anulm timeout-ul dac valoarea se schimb
      // Acest lucru garanteaz c nu se apeleaz setTimeout dac valoarea
      // se schimb n timpul delay-ului
      return () => {
        clearTimeout(handler);
      };
    },
    // Re-run efectul doar cnd valoarea sau delay-ul se schimb
    [value, delay]
  );
  
  return debouncedValue;
}

const TransactionFiltersComponent: React.FC<TransactionFiltersProps> = ({
  type = '',
  category = '',
  subcategory = '',
  onTypeChange = () => {},
  onCategoryChange = () => {},
  onSubcategoryChange = () => {},
  dateFrom = '',
  dateTo = '',
  amountMin = '',
  amountMax = '',
  searchText = '',
  onDateFromChange = () => {},
  onDateToChange = () => {},
  onAmountMinChange = () => {},
  onAmountMaxChange = () => {},
  onSearchTextChange = () => {}
}) => {
  // State pentru expandarea/colapsarea filtrelor avansate
  const [showAdvancedFilters, setShowAdvancedFilters] = useState(false);
  
  // Opiuni pentru tipurile de tranzacii din constante
  const types = OPTIONS.TYPE;
  
  // Categoriile personalizate + predefinite din categoryStore
  const customCategories = useCategoryStore(state => state.categories);
  
  // Construim opiunile pentru categorii din store, nu din constante
  // Respectm regulile: memoizare, fr hardcodri, indicator pentru custom
  const categoryOptions = useMemo(() => {
    // Filtrm categoriile bazate pe tipul de tranzacie selectat
    let filteredCategories = customCategories;
    
    if (type) {
      // Pstrm doar categoriile compatibile cu tipul de tranzacie selectat
      filteredCategories = customCategories.filter(cat => {
        if (type === 'INCOME') return cat.name === 'VENITURI';
        if (type === 'EXPENSE') return cat.name !== 'VENITURI' && cat.name !== 'ECONOMII';
        if (type === 'SAVING') return cat.name === 'ECONOMII';
        return true; // Dac tipul e gol, afim toate
      });
    }
    
    const options = filteredCategories.map(cat => ({
      value: cat.name,
      // Formatm label-ul pentru afiare, incluznd indicator pentru categorii personalizate
      label: cat.name.charAt(0) + cat.name.slice(1).toLowerCase().replace(/_/g, ' ') + 
             (cat.isCustom ? ' ' : '') // Indicator consistent cu cel din TransactionForm
    }));
    
    return options;
  }, [customCategories, type]);
  
  // Obine subcategoriile active folosind noul hook
  const {
    subcategories: activeSubcategories,
    isLoading: isLoadingSubcategories,
    isEmpty: noActiveSubcategories
  } = useActiveSubcategories({
    category,
    type,
    enabled: !!category // Activm doar dac avem o categorie selectat
  });
  
  // Combinm subcategoriile active cu cele din store dac e necesar
  const subcategoryOptions = useMemo(() => {
    // Dac nu avem o categorie selectat, returnm un array gol
    if (!category) return [];
    
    // Dac se ncarc subcategoriile active, afim toate subcategoriile din store ca nainte
    if (isLoadingSubcategories) {
      // Gsim categoria selectat n lista de categorii
      const selectedCategory = customCategories.find(cat => cat.name === category);
      if (!selectedCategory) return [];
      
      // Extragem subcategoriile i le transformm n opiuni pentru Select
      return selectedCategory.subcategories.map(subcat => ({
        value: subcat.name,
        label: subcat.name.charAt(0) + subcat.name.slice(1).toLowerCase().replace(/_/g, ' ') + 
               (subcat.isCustom ? ' ' : '')
      }));
    }
    
    // Dac nu avem subcategorii active sau componentele de cutare returneaz array gol, afim un mesaj n dropdown
    if (noActiveSubcategories || activeSubcategories.length === 0) {
      return [{ 
        value: '', 
        label: INFO.NO_SUBCATEGORIES || TABLE.NO_SUBCATEGORIES,
        disabled: true
      }];
    }
    
    // Altfel, returnm subcategoriile active cu numrul de tranzacii pentru fiecare
          return activeSubcategories.map(subcat => ({
      value: subcat.value,
      label: subcat.label, // Numrul de tranzacii este deja adugat n hook-ul useActiveSubcategories
      disabled: false
    }));
  }, [category, customCategories, isLoadingSubcategories, activeSubcategories, noActiveSubcategories]);

  // Calculm numrul total de filtre active
  const activeFilterCount = useMemo(() => {
    let count = 0;
    if (type) count++;
    if (category) count++;
    if (subcategory) count++;
    if (dateFrom) count++;
    if (dateTo) count++;
    if (amountMin) count++;
    if (amountMax) count++;
    if (searchText) count++;
    return count;
  }, [type, category, subcategory, dateFrom, dateTo, amountMin, amountMax, searchText]);
  
  // Memoizm filtrele combinate pentru a evita recalculri inutile
  const activeFilters = useMemo(() => ({
    type, 
    category, 
    subcategory,
    dateFrom, 
    dateTo, 
    amountMin, 
    amountMax, 
    searchText
  }), [type, category, subcategory, dateFrom, dateTo, amountMin, amountMax, searchText]);

  // Reset subcategory when category changes
  useEffect(() => {
    if (subcategory && (!category || subcategoryOptions.every(opt => opt.value !== subcategory))) {
      onSubcategoryChange('');
    }
  }, [category, subcategory, subcategoryOptions, onSubcategoryChange]);

  // Handler pentru reset global - memoizat
  const handleResetAll = useCallback(() => {
    onTypeChange('');
    onCategoryChange('');
    onSubcategoryChange('');
    onDateFromChange('');
    onDateToChange('');
    onAmountMinChange('');
    onAmountMaxChange('');
    onSearchTextChange('');
  }, [onTypeChange, onCategoryChange, onSubcategoryChange, onDateFromChange, onDateToChange, 
      onAmountMinChange, onAmountMaxChange, onSearchTextChange]);

  // Toggle pentru filtre avansate
  const toggleAdvancedFilters = useCallback(() => {
    setShowAdvancedFilters(prev => !prev);
  }, []);

  // State intern pentru valoarea text input nainte de debounce
  const [searchInputValue, setSearchInputValue] = useState(searchText);
  
  // Aplicm debounce pe valoarea de input
  const debouncedSearchText = useDebounce(searchInputValue, 400); // 400ms delay
  
  // Efect care trimite valoarea debounced ctre parent component
  useEffect(() => {
    if (debouncedSearchText !== searchText) {
      onSearchTextChange(debouncedSearchText);
    }
  }, [debouncedSearchText, searchText, onSearchTextChange]);
  
  // Handler pentru schimbarea valorii de input
  const handleSearchChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchInputValue(e.target.value);
  }, []);

  // Memoizm handlerul pentru schimbarea tipului
  const handleTypeChange = useCallback((e: React.ChangeEvent<HTMLSelectElement>) => {
    const value = e.target.value as TransactionType | '';
    onTypeChange(value);
    // Reset categorie i subcategorie cnd se schimb tipul
    onCategoryChange('');
    onSubcategoryChange('');
  }, [onTypeChange, onCategoryChange, onSubcategoryChange]);

  // Memoizm handlerul pentru schimbarea categoriei
  const handleCategoryChange = useCallback((e: React.ChangeEvent<HTMLSelectElement>) => {
    const value = e.target.value as CategoryType | '';
    onCategoryChange(value);
    // Reset subcategorie cnd se schimb categoria
    onSubcategoryChange('');
  }, [onCategoryChange, onSubcategoryChange]);
  
  // Memoizm handlerul pentru schimbarea subcategoriei
  const handleSubcategoryChange = useCallback((e: React.ChangeEvent<HTMLSelectElement>) => {
    onSubcategoryChange(e.target.value);
  }, [onSubcategoryChange]);

  // Memoizm handlerii pentru filtrele de date
  const handleDateFromChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    onDateFromChange(e.target.value);
  }, [onDateFromChange]);

  const handleDateToChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    onDateToChange(e.target.value);
  }, [onDateToChange]);

  // Memoizm handlerii pentru filtrele de sum
  const handleAmountMinChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    onAmountMinChange(e.target.value);
  }, [onAmountMinChange]);

  const handleAmountMaxChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    onAmountMaxChange(e.target.value);
  }, [onAmountMaxChange]);

  // Utilizm hook-ul de efecte pentru gestionarea efectelor vizuale
  const { getClasses } = useThemeEffects({
    withFadeIn: true,
    withShadow: true,
    withTransition: true
  });

  return (
    <div className={getClasses('flex-group', 'between', 'md')}>
      {/* Bara de filtre compact, pe o singur linie */}
      <div className={getClasses('flex-group', 'start', 'md')}>
        {/* Filtru tip tranzacie */}
        <Select
          name="type-filter"
          label=""
          value={type || ''}
          data-testid="type-filter"
          onChange={handleTypeChange}
          options={types}
          placeholder={PLACEHOLDERS.SELECT + ' tipul'}
          size="sm"
          withTransition
        />
        {/* Filtru categorie */}
        <Select
          name="category-filter"
          label=""
          value={category || ''}
          data-testid="category-filter"
          onChange={handleCategoryChange}
          options={categoryOptions}
          placeholder={PLACEHOLDERS.SELECT + ' categoria'}
          disabled={!type}
          size="sm"
          withTransition
        />
        {/* Filtru subcategorie */}
        <Select
          name="subcategory-filter"
          label=""
          value={subcategory || ''}
          data-testid="subcategory-filter"
          onChange={handleSubcategoryChange}
          options={subcategoryOptions}
          placeholder={isLoadingSubcategories ? LOADER.TEXT : PLACEHOLDERS.SELECT + ' subcategoria'}
          disabled={!category || subcategoryOptions.length === 0}
          isLoading={isLoadingSubcategories}
          size="sm"
          withTransition
        />
      </div>
      {/* Aciuni i searchbox la finalul barei */}
      <div className={getClasses('flex-group', 'end', 'md')}>
        {/* Searchbox compact */}
        <Input
          name="search-text-filter"
          label=""
          value={searchInputValue}
          data-testid="search-text-filter"
          onChange={handleSearchChange}
          placeholder={PLACEHOLDERS.SEARCH}
          type="text"
          withTransition
        />
        {/* Buton reset filtre */}
        {activeFilterCount > 0 && (
          <Button
            variant="ghost"
            size="sm"
            onClick={handleResetAll}
            withShadow
            data-testid="reset-filters-btn"
            aria-label={BUTTONS.RESET_FILTERS}
          >
            {/* Icon reset dac exist, altfel text scurt */}
            {BUTTONS.RESET_FILTERS}
          </Button>
        )}
        {/* Buton filtre avansate (icon sau text scurt) */}
        <Button
          variant="secondary"
          size="sm"
          onClick={toggleAdvancedFilters}
          withShadow
          data-testid="toggle-advanced-filters-btn"
          aria-label={showAdvancedFilters ? UI.TRANSACTION_FILTERS.HIDE_ADVANCED : UI.TRANSACTION_FILTERS.SHOW_ADVANCED}
        >
          {showAdvancedFilters ? UI.TRANSACTION_FILTERS.HIDE_ADVANCED : UI.TRANSACTION_FILTERS.SHOW_ADVANCED}
        </Button>
      </div>
      {/* Filtre avansate - rmn sub bar, vizibile doar dac sunt activate */}
      {showAdvancedFilters && (
        <div className={getClasses('card-section')}>
          <Input
            name="date-from-filter"
            label={LABELS.DATE_FROM_FILTER}
            value={dateFrom}
            data-testid="date-from-filter"
            onChange={handleDateFromChange}
            placeholder={PLACEHOLDERS.SELECT + ' data de nceput'}
            type="date"
            withTransition
          />
          <Input
            name="date-to-filter"
            label={LABELS.DATE_TO_FILTER}
            value={dateTo}
            data-testid="date-to-filter"
            onChange={handleDateToChange}
            placeholder={PLACEHOLDERS.SELECT + ' data de sfrit'}
            type="date"
            withTransition
          />
          <Input
            name="amount-min-filter"
            label={LABELS.AMOUNT_MIN_FILTER}
            value={amountMin}
            data-testid="amount-min-filter"
            onChange={handleAmountMinChange}
            placeholder={PLACEHOLDERS.AMOUNT_MIN_FILTER}
            type="number"
            withTransition
          />
          <Input
            name="amount-max-filter"
            label={LABELS.AMOUNT_MAX_FILTER}
            value={amountMax}
            data-testid="amount-max-filter"
            onChange={handleAmountMaxChange}
            placeholder={PLACEHOLDERS.AMOUNT_MAX_FILTER}
            type="number"
            withTransition
          />
        </div>
      )}
    </div>
  );
};

// Aplicm React.memo pentru a preveni re-rendri inutile
const TransactionFilters = React.memo(TransactionFiltersComponent);

export default TransactionFilters;
