import { useQuery } from '@tanstack/react-query';
import { supabaseService } from '../supabaseService';
import { PAGINATION } from '@shared-constants';
import type { TransactionValidated } from '@shared-constants/transaction.schema';
import { useAuthStore } from '../../stores/authStore';
import { queryKeys, optimizeQueryOptions } from './reactQueryUtils';

// Utilitar pentru formatarea datelor
function pad2(n: number): string {
  return n.toString().padStart(2, '0');
}

/**
 * Genereaz intervalul de date pentru o lun specificat
 * @param year Anul
 * @param month Luna (1-12)
 * @returns Obiect cu datele de nceput i sfrit ale lunii
 */
export function getMonthRange(year: number, month: number): { from: string; to: string } {
  const from = `${year}-${pad2(month)}-01`;
  const days = new Date(year, month, 0).getDate();
  const to = `${year}-${pad2(month)}-${pad2(days)}`;
  return { from, to };
}

export interface UseMonthlyTransactionsOptions {
  includeAdjacentDays?: boolean;
  staleTime?: number;
  gcTime?: number;
}

export interface UseMonthlyTransactionsResult {
  transactions: TransactionValidated[];
  isLoading: boolean;
  error: Error | null;
  totalCount: number;
  refetch: () => Promise<any>;
}

/**
 * Hook specializat pentru ncrcarea tuturor tranzaciilor dintr-o lun specific
 * Spre deosebire de useTransactions, acest hook ncarc toate datele ntr-o singur cerere
 * i nu implementeaz paginare infinit.
 * 
 * @param year Anul pentru care ncrcm tranzaciile
 * @param month Luna pentru care ncrcm tranzaciile (1-12)
 * @param userId ID-ul utilizatorului curent (necesar pentru ncrcare)
 * @param options Opiuni suplimentare pentru query
 * @returns Rezultatul query-ului cu tranzacii, status i funcii de control
 */
export function useMonthlyTransactions(
  year: number,
  month: number, 
  userId?: string,
  options: UseMonthlyTransactionsOptions = {}
): UseMonthlyTransactionsResult {
  // Obinem userId din store dac nu este furnizat explicit
  const { user } = useAuthStore();
  const effectiveUserId = userId || user?.id;
  const {
    includeAdjacentDays = false,
    staleTime = 30 * 1000, // 30 secunde default cache (staleTime)
    gcTime = 5 * 60 * 1000, // 5 minute default pentru garbage collection
  } = options;
  
  // Utilizm queryKeys pentru a genera o cheie standardizat
  const queryKey = queryKeys.transactions.monthly(year, month, effectiveUserId);
  
  // Folosim useQuery standard (nu infinite) pentru a obine toate datele ntr-o cerere
  const query = useQuery(
    optimizeQueryOptions({
      queryKey,
      queryFn: async () => {
        // Calculm intervalul de date pentru luna specificat
        const monthRange = getMonthRange(year, month);
        
        // Ajustm intervalul dac includeAdjacentDays este true
        let dateFrom = monthRange.from;
        let dateTo = monthRange.to;
        
        if (includeAdjacentDays) {
          // Pentru zilele lunii anterioare (ultimele 6 zile)
          const prevMonthYear = month === 1 ? year - 1 : year;
          const prevMonth = month === 1 ? 12 : month - 1;
          const prevMonthDays = new Date(prevMonthYear, prevMonth, 0).getDate();
          const prevMonthStart = `${prevMonthYear}-${pad2(prevMonth)}-${pad2(Math.max(1, prevMonthDays - 5))}`;
          
          // Pentru zilele lunii urmtoare (primele 6 zile)
          const nextMonthYear = month === 12 ? year + 1 : year;
          const nextMonth = month === 12 ? 1 : month + 1;
          const nextMonthEnd = `${nextMonthYear}-${pad2(nextMonth)}-${pad2(6)}`;
          
          // Ajustm intervalul
          dateFrom = prevMonthStart;
          dateTo = nextMonthEnd;
        }
        
        // Facem cererea pentru toate datele din interval
        const result = await supabaseService.fetchTransactions(
          effectiveUserId,
          { 
            limit: 1000, // Limit mare pentru a obine toate tranzaciile
            sort: 'date'
          },
          {
            dateFrom,
            dateTo
          }
        );
        
        return {
          data: result.data,
          count: result.count
        };
      },
      staleTime,
      gcTime,
      // Activm query-ul doar dac avem un userId efectiv disponibil i nu este explcit dezactivat
      enabled: true
    }, effectiveUserId)
  );
  
  // Adaptm rezultatul la interfaa specificat
  return {
    transactions: query.data?.data || [],
    isLoading: query.isLoading || query.isFetching,
    error: query.error as Error | null,
    totalCount: query.data?.count || 0,
    refetch: query.refetch
  };
}
